.main-grid {
  display: grid;
  width: 100%;
  height: 100vh;
  grid-template-columns: 50% 50%;
  grid-template-rows: 10vh 84vh auto 37vh;
  overflow-y: scroll;
}


.main {
  grid-column: 1 / 2;
  grid-row: 2/3;
  background-color: white;
  padding-left: 15vw;
  padding-right: 8vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.title {
  margin-top: -12vh;
    font-family: 'Roboto Condensed', sans-serif;
      font-weight: bold;
      font-size: calc(40px + 0.5vw);
      color: #262A2E;
}

.sub-title {
      font-family: 'Open Sans', sans-serif;
        font-size: calc(14px + 0.5vw);
        color: #4B5158;
        margin-top: 1.4vh;
}

.features {
  background-color: white;
    grid-column: 2 / 3;
    grid-row: 2/3;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    padding-right: 15vw;
    padding-left: 2.5vw;

}

.check-icon {
  width: calc(1vw + 28px);
  height:  calc(1vw + 28px);

  margin-top: 4px;


}

.feature {

    display: flex;
  margin-bottom: 4vh;

}
.feature-text {
  height: 100%;
  font-family: 'Open Sans', sans-serif;
  margin-left: 1.9vh;
  font-size: calc(14px + 0.5vw);
  color: #4B5158;

}

.feature-text span{
  font-weight: 570;
}



.commits {
    background-color: #F0F1EC;
    grid-column: 1 / 3;
    grid-row: 3/4;
    padding: 0 15vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.commits-title {
  color: #262A2E;
  font-family: 'Roboto Condensed', sans-serif;
  text-align: center;
  padding-top: 6vh;
  font-size: calc(35px + 0.5vw);
}

.commits-intro {
  display: flex;
  justify-content: center;
  color: #4B5158;
  font-size: calc(10px + 0.5vw);
  font-family: 'Open Sans', sans-serif;
  font-weight: 500;
  width: 47vw;
  margin: 2vw 0;
}

.commits-intro div {
  text-align: left;
}

.commit-box{

}

.commit {
  background-color: white;
  margin-bottom: 18px;
  border-radius: 6px;
  padding: 1vw;
  width: 47vw;
  color: #4B5158;
  font-size: calc(9px + 0.5vw);
  font-family: 'Open Sans', sans-serif;
  box-shadow: 0px 2px 4px rgba(0,0,0,0.18);
}

.project-title {
color: #373D42;
padding-bottom: 0.7vw;
font-family: 'Roboto Condensed', sans-serif;
display: flex;
justify-content: space-between;
font-size: calc(10px + 0.5vw);
}

.fresh {
  background-color: #FCDF1D;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(5px + 0.35vw);
  padding: 3px 6px;
  color: #FC0000;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 20px;
  font-family: "Open Sans", sans-serif;
}

.commit-message{
  margin-bottom: 0.3vw;
font-style: italic;
  color: #4B5158;
}

.commit-message span{
  color: #858D95;
  font-style: normal;
}

.working-branch {
  margin-bottom: 0.3vw;
  font-style: italic;
}

.working-branch span{
  color: #858D95;
  font-style: normal;
}

.git-remote{
  display: flex;
  justify-content: space-between;
}

.git-remote a{
  text-decoration: none;
  color: #FC0000;
  font-size: calc(9px + 0.25vw);
}

.commit-date{
  color: #858D95;
  font-weight: lighter;
  text-align: right;
  font-size: calc(9px + 0.25vw);
}


.commits-btn {
  background-color: #F0F1EC;
  transition: all 0.06s ease-out;
  padding-bottom: 15px;
  border: 2px solid #F0F1EC;
  box-shadow: 0;
  color: #FC0000;
  font-family: "Open Sans", sans-serif;
  font-size: calc(10px + 0.4vw);
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 38px;
  box-sizing: border-box;
}

.commits-btn:hover {
  border-bottom: 2px solid #Fc0000;
}
.commits-btn:focus {
  outline: 0;
}

.cv {
  text-decoration: none;
  color: #262A2E;
}
