

.bio-grid {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 10vh 55vh 55vh 40vh;
  font-size: calc(8px + 0.5vw);
}

.bio-text1 {
  line-height: 1.4;
  color: #4b5158;
  font-family: "Open Sans";
}


.amore {
  font-size: calc(5px + 0.5vw);
  padding: 10px 0;
  color: #858d95;
}

.bio-title {
  color: #262a2e;
  font-size: calc(35px + 0.5vw);
  font-family: "Roboto Condensed", sans-serif;
  margin: 1.5vw 0;
}



.bio-text2 {
  line-height: 1.4;
  color: #4b5158;
  font-family: "Open Sans", sans-serif;
}
.ny {
    font-size: calc(5px + 0.5vw);
    padding: 10px 0;
      color: #858d95;
}







.link {
  color: #fc0000;
}

@media screen and (min-width: 1400px){

  .bio-text1 {
    padding-top: 1.25vw;
    padding-right:2vw;
    padding-left: 0;
    padding-bottom: 0;
  }


  .bio-text2{
    padding-top: 1.25vw;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 3vw;
  }

  .amore {
    width: 100%;
    text-align: left;
    padding-left: 5%;
  }
  .ny {
    text-align: left;
      width: 100%;

  }

  .bio-image {
    width: 95%;
    margin-top: calc(35px + 3.7vw);
    padding-right: 0;
    padding-bottom: 0;
  }

  .image-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  .bio-image2 {
    width: 95%;
    margin-top: 1.8vw;
    padding-left: 0;
    padding-bottom: 0;
  }

.top-left {
  grid-column: 1/2;
  grid-row: 2/3;
  padding-left: 15vw;
}

.top-right {
  grid-column: 2/3;
  grid-row: 2/3;
  padding-right: 15vw;
  padding-left: 1.5vw;
  padding-top: 1.7vw;
}


.bottom-left {
  grid-row: 3/4;
  grid-column: 1/2;
  padding-left: 15vw;
}

.bottom-right {
  padding-right: 15vw;
  grid-row: 3/4;
  grid-column: 2/3;
}
}

.top-left, .top-right, .bottom-right, .bottom-left {
  background-color: #f0f1ec;
}



@media screen and (max-width: 880px) {
  .top-left {
    grid-column: 1/3;
    padding: 0 5vw;
    grid-row: 2/3;

  }

    .bio-title {
      margin: 4vw 0;
      font-size: calc(30px + 0.5vw);

    }
  .bio-image {
 width: 100%;
 height: 100%;

  }
  .bio-image2 {
    width: 100%;

  }


  .image-container {
    display: flex;
    justify-content: center;

  }

  .amore {
    font-size: calc(6px + 2vw);
padding: 10px 8vw;
  text-align: center;
  }

    .ny {
      font-size: calc(6px + 2vw);
  padding: 10px 8vw;
    text-align: center;
    }
  .bio-text1, .bio-text2 {
    padding: 0 0 5vw 0;
  }

  .bio-grid {
    grid-template-rows: 10vh auto auto auto auto auto;
    font-size: calc(8px + 2vw);

  }

  .top-right {
    grid-column: 1/3;
    padding-right: 0;
    grid-row: 3/4;
    z-index: 1000;

  }

  .bottom-right {
    grid-column: 1/3;
    grid-row: 4/5;
    padding: 0 5vw;
    padding-top: 5vw;


  }
  .bottom-left {
    grid-column: 1/3;
    grid-row: 5/6;
    padding-top: 5vw;
  }

  .footer {
    grid-row: 6/7;
  }
}
