
.footer {
  grid-row: 4/5;
  grid-column: 1/3;

  display: flex;
  flex-direction: column;
  padding: 0 15vw;
  align-items: center;
  justify-content: center;
}

.footer-text {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  color: #4B5158;
