}
.footer-text span {
  font-weight: bold;
  color: #262A2E;

}


.footer-title {
  font-family: 'Roboto Condensed', sans-serif;
  text-align: center;
  font-size: calc(35px + 0.5vw);
  color: #262A2E;
}

.footer-footer {
color: #4B5158;
font-size: calc(8px + 0.5vw);
  width: 100%;
  margin-top: 28px;
  line-height: 1.8;
  text-align: center;
}

.footer-footer-bye {
  color: #FC0000;
  font-size: calc(6px + 0.5vw);
  padding-top: 1vw;
  font-family: "Roboto Condensed", sans-serif;

}

.footer-footer-bye div {
  color: #858D95;
  font-family: serif;
}


